<template>
    <div>
    <div>
      <el-button  type="primary" style="margin-left: 100px" @click="main"></el-button>
      <el-button  v-if="this.userid === ''" type="primary" style="margin-left: 950px" @click="denglu"></el-button>
      <el-button  v-if="this.userid != ''" type="primary" style="margin-left: 950px" @click="user">{{this.userid}}</el-button>
      <el-button  type="primary"  @click="car"></el-button>
      <el-button  type="primary"  @click="chong"></el-button>
      <el-button  type="primary"  @click="tui"></el-button>
    </div>
  <div class="login" >
    <p> :{{m}}</p>
    <el-input  clearable  v-model.trim="v" autocomplete="off" placeholder="/"  style="width: 250px"></el-input>
    <el-button  class="c" type="primary" @click="chongzhi"></el-button>
  </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      userid: '',
      userword: '',
      m: 0,
      v: ''
    }
  },
  mounted () {
    this.get()
  },
  methods: {
    car () {
      if (sessionStorage.getItem('userid') === '') {
        this.$router.push('/login')
      } else {
        this.$router.push('/car')
      }
    },
    main () {
      this.$router.push('/')
    },
    denglu () {
      this.$router.push('/login')
    },
    chong () {
      if (sessionStorage.getItem('userid') === '') {
        this.$router.push('/login')
      } else {
        this.$router.push('/chongzhi')
      }
    },
    user () {
      this.$router.push('/personalinfo')
    },
    tui () {
      sessionStorage.setItem('userid', '')
      sessionStorage.setItem('userword', '')
      this.$router.push('/')
    },
    get () { // 
      this.userid = sessionStorage.getItem('userid')
      this.userword = sessionStorage.getItem('userword')
      let formData = new FormData()
      formData.append('id', sessionStorage.getItem('userid'))
      formData.append('word', sessionStorage.getItem('userword'))
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      this.$axios.post('http://tendoyun.free.idcfengye.com/personal', formData, config
      )
        .then(Response => {
          this.m = Response.data[0].money
          sessionStorage.setItem('money', this.m)
        })
    },
    chongzhi () {
      let formData = new FormData()
      formData.append('id', sessionStorage.getItem('userid'))
      formData.append('word', sessionStorage.getItem('userword'))
      formData.append('money', this.m)
      formData.append('v', this.v)
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      this.$axios.post('http://tendoyun.free.idcfengye.com/chongzhi', formData, config
      )
        .then(Response => {
          this.get()
        })
    }
  }
}
</script>
<style type="text/css">
.login{
position: absolute;/**/
width: 300px;
height: 200px;
text-align: center;/*(div)*/
top: 50%;
left: 50%;
margin-top: -200px;
margin-left: -150px;
}
.c {
    margin-top: 20px;
}
</style>



