<template>
  <div >
    <div>
      <el-button  type="primary" style="margin-left: 100px" @click="main"></el-button>
      <el-button  v-if="this.userid === ''" type="primary" style="margin-left: 950px" @click="denglu"></el-button>
      <el-button  v-if="this.userid != ''" type="primary" style="margin-left: 950px" @click="user">{{this.userid}}</el-button>
      <el-button  type="primary"  @click="car"></el-button>
      <el-button  type="primary"  @click="chong"></el-button>
      <el-button  type="primary"  @click="tui"></el-button>
    </div>
      <el-container style="height: 700px; border: 1px solid #eee">
        <el-header  style="background-color: rgb(238, 241, 246)">
            <p6 style="margin-left: 25px;height: 100px;font-size:22px;"></p6>
            <el-button  type="primary" style="margin-left: 500px" @click="allgood"></el-button>
            <el-button  type="primary" @click="daifahuo" ></el-button>
            <el-button  type="primary" @click="tuikuan" ></el-button>
            <el-button  type="primary" @click="yiwancheng" ></el-button>
            <el-button  type="primary" @click="goodmanage" ></el-button>
            <el-button  type="primary" @click="addgood"></el-button>
        </el-header>
        <el-main style="height:0;flex-grow:1;">
            <el-table :data="tableData.slice((currentPage - 1) * pageSize, currentPage*pageSize)"
            @row-click="openDetails" border>
                <el-table-column prop="id" label="ID" width="80">
                </el-table-column>
                <el-table-column prop="goodid" label="ID" width="100">
                </el-table-column>
                <el-table-column prop="name" label="" width="80">
                </el-table-column>
                <el-table-column prop="price" label="" width="90">
                </el-table-column>
                <el-table-column prop="intro" label="" width="160">
                </el-table-column>
                <el-table-column prop="newo" label="/" width="80">
                </el-table-column>
                <el-table-column prop="fenlei" label="" width="60" >
                </el-table-column>
                <el-table-column prop="size" label="" width="90" >
                </el-table-column>
                <el-table-column prop="yijia" label="" width="60" >
                </el-table-column>
                <el-table-column prop="count" label="" width="60" >
                </el-table-column>
                <el-table-column prop="address" label="" width="110" >
                </el-table-column>
                <el-table-column prop="time" label="" width="100" >
                </el-table-column>
                <el-table-column prop="imgUrl" label="" width="310" >
                    <template slot-scope="scope">
                        <el-carousel  style="width: 300px; height:300px" >
                            <el-carousel-item v-for="i in scope.row.imgUrl" :key="i">
                            <el-image style="width: 300px; height:300px"
                                :src="i" v-if="i!==null"></el-image>
                            </el-carousel-item>
                        </el-carousel>
                    </template>
                </el-table-column>
                <el-table-column  width="80" label="">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" @click.native.prevent="deleteRow(scope.$index, tableData)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-main>
        <el-footer>
            <el-pagination
                @size-change="sizechange"
                @current-change="currentchange"
                style="margin-left: 1010px"
                background
                :current-page="currentPage"
                :page-size="pageSize"
                layout="prev, pager, next"
                :total="length">
            </el-pagination>
        </el-footer>
    </el-container>
    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="quxiao"> </el-button>
        <el-button type="primary" @click="sure"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      userid: '',
      userword: '',
      centerDialogVisible: false,
      tableData: [],
      currentPage: 1,
      pageSize: 8,
      length: 0,
      index: '',
      rows: '',
      column: '',
      str: [],
      data: []
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    car () {
      if (sessionStorage.getItem('userid') === '') {
        this.$router.push('/login')
      } else {
        this.$router.push('/car')
      }
    },
    main () {
      this.$router.push('/')
    },
    denglu () {
      this.$router.push('/login')
    },
    chong () {
      if (sessionStorage.getItem('userid') === '') {
        this.$router.push('/login')
      } else {
        this.$router.push('/chongzhi')
      }
    },
    user () {
      this.$router.push('/personalinfo')
    },
    tui () {
      sessionStorage.setItem('userid', '')
      sessionStorage.setItem('userword', '')
      this.$router.push('/')
    },
    allgood () {
      this.$router.push('/shopmanage')
    },
    daifahuo () {
      this.$router.push('/sendgood')
    },
    tuikuan () {
      this.$router.push('/tuikuan')
    },
    yiwancheng () {
      this.$router.push('/finished')
    },
    addgood () {
      this.$router.push('/addgood')
    },
    goodmanage () {
      this.$router.push('/xiajia')
    },
    openDetails (row) { // 
      this.column = row
    },
    getData () { // 
      this.userid = sessionStorage.getItem('userid')
      this.userword = sessionStorage.getItem('userword')
      let formData = new FormData()
      formData.append('id', sessionStorage.getItem('userid'))
      formData.append('word', sessionStorage.getItem('userword'))
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      this.$axios.post('http://tendoyun.free.idcfengye.com/sendgood', formData, config)
        .then(res => {
          for (var i = 0; i < res.data.length; i++) {
            this.str = res.data[i].imgUrl.split(',')
            this.str.pop()
            res.data[i].imgUrl = this.str
          }
          this.tableData = res.data
          for (let k = 0; k < this.tableData.length; k++) {
            for (let j = 0; j < this.tableData[k].imgUrl.length; j++) {
              this.tableData[k].imgUrl[j] = 'http://tendoyun.free.idcfengye.com/img/' + this.tableData[k].imgUrl[j]
            }
          }
          for (let f = 0; f < this.tableData.length; f++) {
            this.tableData[f].address = ':' + this.tableData[f].phone + '\xa0\xa0\xa0\xa0\xa0\xa0:' + this.tableData[f].uname +
            '\xa0\xa0\xa0\xa0\xa0\xa0:' + this.tableData[f].address
          }
          this.length = this.tableData.length
        })
    },
    deleteRow (index, rows) {
      this.centerDialogVisible = true
      this.index = (this.currentPage - 1) * 4 + index
      this.rows = rows
    },
    sure () { // 
      this.centerDialogVisible = false
      let formData = new FormData()
      formData.append('id', this.column.id)
      formData.append('shopid', sessionStorage.getItem('userid'))
      formData.append('word', sessionStorage.getItem('userword'))
      formData.append('goodid', this.column.goodid)
      let config = {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      this.$axios.post('http://tendoyun.free.idcfengye.com/fahuo', formData, config)
        .then(res => {
          this.getData()
          this.$notify({
            title: '',
            message: '',
            type: 'success'
          })
        })
    },
    quxiao () {
      this.centerDialogVisible = false
      this.$notify({
        title: '',
        message: '',
        type: 'warning'
      })
    },
    sizechange (val) {
      this.pageSize = val
    },
    currentchange (val) {
      this.currentPage = val
    }
  }
}
</script>
<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 50px;
  }
</style>



